      <div class="box">
          <div class="wz_title">{{ item.title }}</div>
        <!-- 大图片 -->
        <div class="big_img">
          <div  class="big_img2">
            <div>
              <img style="width: 350px; height: 350px;" :src="src2" />
            </div>
            <!-- 小图片 -->
            <div class="box_bottom">
              <div
                class="x_tp"
                v-for="item2 in sp[0].imgarr"
                :key="item2.id"
              >
                <img style="cursor: pointer;" @click="chang_img(item2.id)" :src="item2.src" alt="" />
              </div>
            </div>
          </div>

        </div>
      </div>
      <div class="wz_top">
            
            <div style="border: 1px solid black"></div>
            <div class="wz">
              <span style="color: #828282">价格：</span
              ><span style="font-szie: 30px; color: #2b2b2b; font-weight: 900">
                ￥{{ item.price }}</span
              >
            </div>
            <div class="wz">
              <span style="color: #828282">编号：</span>{{ item.number }}
            </div>
            <div class="wz">
              <span style="color: #828282">类型：</span>{{ item.type }}
            </div>
            <div class="wz">
              <span style="color: #828282">规格：</span>{{ item.norm }}
            </div>
            <div class="wz">
              <span style="color: #828282">品牌：</span>{{ item.pp }}
            </div>
            <div class="wz" style="color:black;margin-top:20px;">
              想要预约或者购买茶叶可以点击右上角的分享，点击客服二维码，扫描二维码添加微信客服
            </div>
          </div>
      <div
        class="btn"
        v-for="item in sp"
        :key="item.id + 'a'"
      >
        <div>
          <el-button @click="last(4)">上一页</el-button
          >
        </div>
        <div>
          <el-button @click="next(6)">下一页</el-button
          >
        </div>
      </div>

        <!-- 分割线 -->
      <div class="fgx">
        <el-divider><span style="font-size: 15px">商品详情</span></el-divider>
      </div>
      <!-- 详情 -->
      <div class="b">
        <el-image
          style="width: 600px"
          :src="item.b_img"
        >
        </el-image>
      </div>
    </div>
  </div>
</template>

<script>
import Navbar from '../Navbar.vue';
export default {
  components: { Navbar },
  name: "Sp",
  data() {
    return {
      id: "",
      src2: "",
      sp: [
        {
          id: 1,
          title: "大树有机茶（生茶）",
          price: "1680.00",
          number: "A019",
          type: "普洱茶",
          norm: "357g",
          pp:'万源共享',
          b_img: 'https://image.baidu.com/search/down?url=https://tvax4.sinaimg.cn/large/006D2oSpgy1h1805tnsxqj30kj6bknav.jpg',
          srcList: ['https://image.baidu.com/search/down?url=https://tvax4.sinaimg.cn/large/006D2oSpgy1h1805tnsxqj30kj6bknav.jpg'],
          imgarr: [
            {
              id: 1,
              src: 'https://image.baidu.com/search/down?url=https://tvax2.sinaimg.cn/large/006D2oSpgy1h1806190lnj301o01o3yb.jpg',
              src2: 'https://image.baidu.com/search/down?url=https://tvax1.sinaimg.cn/large/006D2oSpgy1h1806anme9j30b40b4gn5.jpg',
            },
            {
              id: 2,
              src: 'https://image.baidu.com/search/down?url=https://tva3.sinaimg.cn/large/006D2oSpgy1h18061cseqj301o01ojr6.jpg',
              src2: 'https://image.baidu.com/search/down?url=https://tva3.sinaimg.cn/large/006D2oSpgy1h1806asa7lj30m80m8tdl.jpg',
            },
          ],
        },
      ],
    };
  },
  methods: {
    get_id(id) {
      this.id = this.$route.query.id;
      document.title = this.sp[0].title;
      this.src2 = this.sp[0].imgarr[0].src2;
    },
    chang_img(img_id) {
        this.src2 = this.sp[0].imgarr[img_id - 1].src2;
    },
    last(id) {
      this.$router.push("/sp_"+id+"?id=" + id);
      this.get_id();
    },
    next(id) {
      this.$router.push("/sp_"+id+"?id=" + id);
      this.get_id();
    },
  },
  created() {
    this.get_id();
  },
  mounted() {},
};
</script>

